.sidebar-nav {
    width: calc(100% - 60px);
    height: 100%;
    background-color: #fff;
    padding: 0px 30px 0px 30px;
    -webkit-box-shadow: 0px 0px 5px 1px #c1c9c9;
    -moz-box-shadow:  0px 0px 5px 1px #c1c9c9;
    box-shadow: 0px 0px 5px 1px #c1c9c9;
    z-index: 100;
    overflow: hidden;
    
}

.sidebar-nav-head {
    height: 25vh;
    width: 100%;
    /* background-color: #fbfedc; */
    /* clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%); */
    display: grid;
    grid-template-columns: 2fr 2fr 1fr;
    align-content: center;
}

.sidebar-nav-header-username{
    display: grid;
    font-size: 2.0em;
}

.sidebar-nav-header-username p {
    align-self: center;
    /* font-size: 20px; */
}
.sidebar-nav-header-icon img {
    clip-path: circle(40%);
    max-width: 100%;
    cursor: pointer;
}

.sidebar-nav-links {
    height: 100%;
}

/* a {
    color: none;
} */

.sidebar-nav-links-scrollable {
    color: rgba(0,0,0,0.6) !important;
    padding-left: 20px;
    display: grid;
    grid-template-rows: auto;
}

.sidebar-nav-links-scrollable div {
    height: 50px;
}

.sidebar-nav-links-scrollable-link {
    color: rgba(0,0,0,0.6);
    display: grid; 
    grid-template-columns: 1fr 4fr;
}

.sidebar-nav-links-scrollable-link:hover {
    color: rgba(0,0,0,1);
    font-size: 1.2em;
    cursor: pointer;
}

.modal-photo-upload {
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: auto;
    left: 0;
    top: 0;
    display: grid;
    align-items: center;
    justify-items: center;
    background-color: rgba(192,192,192,0.5)
    
}

.modal-photo-upload-content {
    
    width: 50%;
    height: 50%;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 5px 1px #c1c9c9;
    -moz-box-shadow:  0px 0px 5px 1px #c1c9c9;
    box-shadow: 0px 0px 5px 1px #c1c9c9;
    z-index: 1001;
    display: grid;
    grid-template-rows: 1fr 7fr;
    opacity: 1 !important;
    
}

.modal-photo-upload-close {
    padding: 10px 10px 0 0;
}

.close {
    justify-self: end;
    font-size: 3em;
    padding: 10px 10px 0 0;
}

.modal-photo-upload-form-container {
    width: 100%;
    height: 100%;
    display: grid;
}

.modal-photo-upload-form {
    justify-self: center;
    width: 50%;
    display: grid;
    grid-template-rows: 6fr 1fr 1fr;
}

